import React from "react";
import { Link, } from "gatsby";
import { rhythm, scale } from "../utils/typography";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTag } from '@fortawesome/free-solid-svg-icons';

export const Post = node => {
  const title = node.frontmatter.title || node.fields.slug;
  return (
    <article>
      <header className="index-post-header" style={{display: "flex"}}>
        <h3 style={{marginLeft: rhythm(1.4)}}>
          <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
            {node.frontmatter.date}&nbsp;&nbsp;&nbsp;&nbsp;{title}
          </Link>
        </h3>
      </header>
      <section>
      </section>
    </article>
  );
};

export const Tag = ({ tag }) => {
  const tagName = tag[0];
  const tagNum = tag[1];
  return (
    <h4 className="index-tag-one">
      <Link style={{ boxShadow: `none` }} to={`tags/${tagName}/page1`}>
        <FontAwesomeIcon icon={faTag} style={{marginRight: "10px"}}/>
        {tagName}({tagNum})
      </Link>
    </h4>
  );
};
